import React from 'react';
import { connect } from 'react-redux';
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import {Table} from 'reactstrap';
import { getLoans, createLoans, updateLoans, deleteLoans, approveLoan } from 'actions/ManageLoans';
import IntlMessages from 'util/IntlMessages';


class manageLoans extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            modal: false,
            loan: {
                level: "",
                duration: "",
                amount: "",
                interest_rate: ""
            },
            required: false,
            submitText: 'submit'
        }
    }

    componentDidMount(){
        this.props.getLoans();
    }

    toggle(params, index, id){
        switch (params){
            case 'create':
                this.setState({
                    ...this.state,
                    modal: !this.state.modal,
                    loan : {
                        ...this.state.loan,
                        level: '',
                        duration: '',
                        amount: '',
                        interest_rate: ''
                    },
                    submitText: 'submit'
                });
                break;
            case 'edit':
                this.setState({
                    modal: !this.state.modal
                }, () => {
                    this.props.loans.loans.forEach((item) => {
                       if(item.id === id){
                           console.log(item);
                           this.setState({
                               ...this.state,
                               loan: {
                                   ...this.state.loan,
                                   level: item.level,
                                   loan_id: item.id,
                                   duration: item.duration,
                                   amount: item.amount,
                                   interest_rate: item.interest_rate
                               },
                               submitText: 'update'
                           }, () => {
                               console.log(this.state);
                           });
                       }
                    });
                });
                break;
            case 'delete':
                  let data = {
                      loan_id: id
                  }
                  console.log(data);
                  this.props.deleteLoans(data);
                break;
            case 'approveLoan':
                let datas  = {
                    request_id: id
                }
                this.props.approveLoan(datas);
                break;
            default:
                this.setState({
                    modal: !this.state.modal
                });
        }

    }

    submit(params) {
        if(params === 'cancel'){
            this.toggle();
        }else if((params === 'submit') && (this.state.submitText === 'submit')){
            if(this.state.loan.level !== '' || !this.state.loan.amount !== '' || !this.state.loan.interest_rate !== '' || this.state.loan.duration){
                this.props.createLoans(this.state.loan);

            }else{
                this.setState({
                    ...this.state,
                    required: true
                })
            }
        }else if((params === 'submit') && (this.state.submitText === 'update')){
            if(this.state.loan.level !== '' || this.state.loan.duration !== '' || this.state.loan.interest_rate !== '' || this.state.loan.amount !== '' ) {
                this.props.updateLoans(this.state.loan);
            }
        }

    }

    changeValue(e){
        this.setState({
            ...this.state,
            loan: {
                ...this.state.loan,
                [e.target.name]: e.target.value,
            },
            required: false

        }, () => {
            console.log(this.state.loan);
        })
    }


    render(){
        return (
            <div className={'loanContainer'}>
                {this.props.loans.loanDeletionMessage !== {} ? <div className={'successCustom'}>{this.props.loans.loanDeletionMessage.Message}</div> : ''}
                <Button className="jr-btn btn-primary text-white mb-0" color="primary" onClick={() =>this.toggle('create', null, null)}>
                    <i className="zmdi zmdi-plus" style={{paddingRight: 5}}/>
                    <span>Create Loans</span>
                </Button>
                <div className="row">
                    <div className="col-12">
            <div className="jr-card">
            <div className="jr-card-header d-flex align-items-center">
                <h3 className="mb-0"><IntlMessages id="faq.category"/></h3>
                <div className="ml-3">
                                <span className="text-white badge badge-success">
                                    <IntlMessages id="faq.aboutLender"/></span>
                </div>
            </div>
            <div className="table-responsive-material">
                <Table>
                    <thead>
                    <tr>
                        <th className="border-top-0">S/N</th>
                        {/*<th className="border-top-0">Payment Terms</th>*/}
                        <th className="border-top-0">Interest rate</th>
                        <th className="border-top-0">Amount</th>
                        <th className="border-top-0">Level</th>
                        <th className="border-top-0">Actions</th>
                    </tr>
                    </thead>
                    <tbody>

                    {this.props.loans.loans.map((item, index) =>  {
                        return (
                            <tr key={item.id}>
                                <td>{index + 1}</td>
                                {/*<td>{item.payment_terms}</td>*/}
                                <td>{item.interest_rate}</td>
                                <td>{item.amount}</td>
                                <td>{item.level}</td>
                                <td>
                                    <div>
                                        <Button className="jr-btn btn-success text-white mb-0" color="success" onClick={() =>this.toggle('approveLoan', index, item.id)}>
                                            <i className="zmdi zmdi-check" style={{paddingRight: 5}}/>
                                            <span>Approve</span>
                                        </Button>

                                        <Button className="jr-btn btn-primary text-white mb-0" color="primary" onClick={() =>this.toggle('edit', index, item.id)}>
                                            <i className="zmdi zmdi-comment-edit" style={{paddingRight: 5}}/>
                                            <span>Edit</span>
                                        </Button>

                                        <Button className="jr-btn btn-danger text-white mb-0" color="danger" onClick={() =>this.toggle('delete', index, item.id)}>
                                            <i className="zmdi zmdi-delete" style={{paddingRight: 5}}/>
                                            <span>Delete</span>
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        );
                    })}
                    </tbody>
                </Table>
            </div>
            </div>
                    </div>
                </div>

                <Modal isOpen={this.state.modal} toggle={() => this.toggle()} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Create Loans</ModalHeader>
                    <ModalBody>
                        {this.props.loans.loanCreateMessage !== {} ? <div className={'successCustom'}>{this.props.loans.loanCreateMessage.Message}</div> : ''}
                        {this.state.required ? <div className={'requiredField'}>All fields are required</div> : null}

                        <div className="form-group row">
                            <label htmlFor={'level'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Level *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" className="form-control" id="level"
                                       placeholder="Level" name={'level'} onChange={(event) =>this.changeValue(event)}
                                       value={this.state.loan.level}/>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor={'duration'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Duration *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" name={'duration'}  id="duration" placeholder="Duration"
                                       onChange={(event) =>this.changeValue(event)} className="form-control"
                                       value={this.state.loan.duration}/>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor={'amount'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Amount *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" name={'amount'}  id="amount" placeholder="Amount"
                                       onChange={(event) =>this.changeValue(event)} className="form-control"
                                       value={this.state.loan.amount}/>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor={'interest_rate'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Interest rate *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" name={'interest_rate'}  id="interest_rate" placeholder="Interest rate"
                                       onChange={(event) =>this.changeValue(event)} className="form-control"
                                       value={this.state.loan.interest_rate}/>
                            </div>
                        </div>

                    </ModalBody>
                    <ModalFooter>
                        <Button color="link" onClick={() =>this.submit('cancel')}> Cancel</Button>{' '}
                        <Button color="link" onClick={() => this.submit('submit')}>{this.state.submitText}</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}


const mapStateToProps = (state) => ({
   loans: state.manageLoans
});

export default connect(mapStateToProps, {getLoans, createLoans, updateLoans, deleteLoans, approveLoan})(manageLoans);


